@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@300;400;500;600&display=swap');
@import '../style/vars.scss';

html{
  box-sizing: border-box;
}

body {
  margin: 0;
  color: #fff;
  @extend %os-regular;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul { list-style: none; padding-left: 0;}

.app {
  position: relative;
  padding-bottom: 15px;
  padding-top: 80px;
  min-height: 100vh;
  background-image: url('https://i.ibb.co/qxVW9F7/Pngtree-cinema-stage-333472.jpg');
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;

  &__left{
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 300px;
    background: rgb(35, 35, 35);
    border-right: 1px solid rgba(255, 255, 255, 0.14);
    position: absolute;
    padding:70px 10px 15px;
    top: 0;
    bottom: 0;

    h3 {
      font-size: 24px;
      letter-spacing: 1px;
      @extend %os-regular;
      margin-bottom: 0;
    }
  }

  &__right{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-left: 350px;
    margin-right: 50px;

    h3 {
      font-size: 22px;
    }
  }
}

label {
  color: #fff !important;
}

// progressbar 

.css-kcj98w {
  display: flex !important;
  align-items: center;
  justify-content: center;
}

// slider-swipe

.swiper {
  padding-right: 15px !important;
}

// switch

.MoviePage_moviePage__tOasz span {
  @extend %os-regular;
  font-size: 18px;
  line-height: 1.5;
}